.tasks {
  &:not(:last-of-type) {
    margin-bottom: 40px;
  }

  &__title {
    font-family: 'Montserrat', sans-serif;
    font-weight: bold;
    font-size: 32px;
    line-height: 39px;
    color: #64c4ed;
    padding-bottom: 20px;
    border-bottom: 1px solid #f2f2f2;

    &:hover {
      img {
        opacity: 0.2;
      }
    }
  }

  img {
    margin-left: 15px;
    opacity: 0;
    cursor: pointer;

    &:hover {
      opacity: 0.8;
    }
  }

  &__form {
    margin-top: 16px;

    img {
      opacity: 1;
      margin: 0;
      margin-left: 2px;
    }

    &-block {
      .button {
        margin-right: 10px;
        margin-top: 15px;
      }
    }

    &-new {
      display: flex;
      align-items: center;
      cursor: pointer;
      opacity: 0.4;
      transition: opacity 0.15 ease-in-out;

      &:hover {
        opacity: 0.8;
      }

      span {
        font-size: 16px;
        margin-left: 20px;
      }
    }
  }

  &__items {
    margin-top: 30px;

    h2 {
      font-family: 'Montserrat';
      font-weight: bold;
      font-size: 20px;
      color: #c9d1d3;
      position: absolute;
      left: 50%;
      top: 50%;
    }

    &-row {
      display: flex;
      align-items: center;
      margin-bottom: 15px;

      &:hover &-actions {
        opacity: 1;
      }

      &-actions {
        display: flex;
        opacity: 0;
        transition: opacity 0.15s ease-in-out;

        div {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 32px;
          height: 32px;
          border-color: lighten(#f4f6f8, 2%);
          border-radius: 6px;
          margin-left: 5px;
          cursor: pointer;
        }

        &:hover {
          background-color: darken(#f4f6f8, 2%);

          svg {
            opacity: 0.9;
          }
        }
      }

      svg {
        width: 11px;
        height: 11px;
        opacity: 0.4;
      }

      p {
        margin-left: 15px;
        border: 0;
        font-size: 16px;
        width: 100%;
      }
    }
  }
}

.checkbox {
  input {
    display: none;
  }

  svg {
    transition: opacity 0.15s ease-in-out;

    path {
      stroke: #f2f2f2;
    }
  }

  input:checked + label {
    background-color: #4dd599;
    border-color: #4dd599;

    svg {
      opacity: 1;
      path {
        stroke: #fff;
      }
    }
  }

  &:hover {
    label {
      background-color: #f2f2f2;
      border-color: #f2f2f2;
      svg {
        opacity: 1;

        path {
          stroke: #b2b2b2;
        }
      }
    }
  }

  label {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background-color 0.15s ease-in-out, border-color 0.15s ease-in-out;
    border: 2px solid #e8e8e8;
    border-radius: 30px;
    cursor: pointer;
    width: 20px;
    height: 20px;

    svg {
      opacity: 0;
    }
  }
}
